import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import { actSearchRequest } from "./../actions/index";
import { actFetchClassesRequest } from "./../actions/index";
import {actSearchInputRequest} from './../actions/index';
import { connect } from "react-redux";

class PanelLeft extends Component {
    
    handleOnClick = event => {
        let searchInfo = {
            classTeach: event.target.id,
            subject: "",
            district: "",
            isSearch: true
          };
        this.props.onSearch(searchInfo, 0);
        this.props.onSearchInput(searchInfo);
    }
    handleOnClickSubject = event => {
        let searchInfo = {
            classTeach: "",
            subject: event.target.id,
            district: "",
            isSearch: true
          };
        this.props.onSearch(searchInfo, 0);
        this.props.onSearchInput(searchInfo);
    }
    handleOnClickDistrict = event => {
        let searchInfo = {
            classTeach: "",
            subject: "",
            district: event.target.id,
            isSearch: true
          };
        this.props.onSearch(searchInfo, 0);
        this.props.onSearchInput(searchInfo);
    }
    handleOnClickMore = () => {
        let searchInfo = {
            classTeach: "",
            subject: "",
            district: "",
            isSearch: false
          };
        this.props.onSearchInput(searchInfo);
        this.props.fetchAllClasses(0);
    }
    render() {
       
        return (
            <div className="col-lg-3 col-md-3 col-sm-3 mx-auto" id="col-left">
                <div id="left">
                    <div className="list-group">
                        <h4 className="widget-title">Lp cn gia s</h4>
                        <Link className="list-group-item" to='/classes/Lp 1' id='Lp 1' onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 1
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 2' id='Lp 2'  onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 2
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 3' id="Lp 3" onClick={this.handleOnClick}>
                            <i className="fa fa-play"
                                style={{ color: '#F28E11' }} />
                                Lp 3
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 4' id="Lp 4" onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 4
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 5' id="Lp 5" onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 5
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 6' id="Lp 6" onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 6
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 7' id="Lp 7" onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 7
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 8' id="Lp 8" onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 8
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 9' id="Lp 9" onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 9
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 10' id="Lp 10" onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 10
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 11' id="Lp 11" onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 11
                        </Link>
                        <Link className="list-group-item" to='/classes/Lp 12' id="Lp 12" onClick={this.handleOnClick}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Lp 12
                        </Link>
                        <h4 className="widget-title">Tm lp theo mn</h4>
                        <Link className="list-group-item" to='/classes/Ton' id="Ton" onClick={this.handleOnClickSubject}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Ton
                        </Link>
                        <Link className="list-group-item" to='/classes/L' id="L" onClick={this.handleOnClickSubject}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                L
                        </Link>
                        <Link className="list-group-item" to='/classes/Ho' id="Ho" onClick={this.handleOnClickSubject}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Ho
                        </Link>
                        <Link className="list-group-item" to='/classes/Ting Anh' id="Ting Anh" onClick={this.handleOnClickSubject}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Ting Anh
                        </Link>
                        <h4 className="widget-title">Tm lp theo qun</h4>
                        <Link className="list-group-item" to='/classes/Qun 1' id="Qun 1" onClick={this.handleOnClickDistrict}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Qun 1
                        </Link>
                        <Link className="list-group-item" to='/classes/Qun 2' id="Qun 2" onClick={this.handleOnClickDistrict}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Qun 2
                        </Link>
                        <Link className="list-group-item" to='/classes/Qun 3' id="Qun 3" onClick={this.handleOnClickDistrict}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Qun 3
                        </Link>
                        <Link className="list-group-item" to='/classes/Qun 4' id="Qun 4" onClick={this.handleOnClickDistrict}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Qun 4
                        </Link>
                        <Link className="list-group-item" to='/classes/Qun 5' id="Qun 5" onClick={this.handleOnClickDistrict}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Qun 5
                        </Link>
                        <Link className="list-group-item" to='/class-list'  onClick={this.handleOnClickMore}>
                            <i className="fa fa-play" 
                                style={{ color: '#F28E11' }} />
                                Xem thm
                        </Link>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
      classes: state.classes,
    };
  };
  
  const mapDispatchToProps = dispatch => {
    return {
      onSearch: (searchInfo, pageSearch) => {
        dispatch(actSearchRequest(searchInfo, pageSearch));
      },
      onSearchInput: (search) => {
        dispatch(actSearchInputRequest(search));
      },
      fetchAllClasses: page => {
        dispatch(actFetchClassesRequest(page));
      },
    };
  };

export default connect(mapStateToProps, mapDispatchToProps)(PanelLeft);