import React, { Component } from 'react';
import "./../style/signup.css";
import "antd/dist/antd.css";
import { Form, Input, Button, Select, notification} from "antd";
import {connect} from 'react-redux';
import {actAddClassRequest} from './../actions/index';

const { Option } = Select;

class RegisterToFindTutorPage extends Component {
  handleSubmit = e => {
      e.preventDefault();
      var {history} = this.props;
      this.props.form.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
          //send request add class
          this.props.onAddClass(values, history);
        } else {
          notification.error({
            message: "Error",
            description: "Error add class"
          });
        }
         
      });
  };

  render() {
      const { getFieldDecorator } = this.props.form;
      return (
          <div className="col-lg-9 col-md-9 col-sm-9">
              <div className="row">
                  <div className="panel-heading">
                      <i className="fa fa-pencil-square" style={{ marginLeft: '5px' }} />ng k tm gia s
                  </div>
              </div>
              <div className="row" id="row-form">
                  <div className="panel-body">
                     
                      <Form
                      layout="vertical"
                      className="formal-form"
                      onSubmit={this.handleSubmit}
                      >
                          <Form.Item label="a ch: ">
                              {getFieldDecorator("address", {
                              rules: [
                                  {
                                  required: true,
                                  message: "Vui lng nhp a ch"
                                  }
                              ]
                              })(<Input placeholder="Nhp s nh, ng..." />)}
                          </Form.Item>
                          <Form.Item label="Qun" hasFeedback>
                              {getFieldDecorator("district", {
                              rules: [
                                  {
                                  required: true,
                                  message: "Vui lng chn qun!"
                                  }
                              ]
                              })(
                              <Select placeholder="Chn qun...">
                                  <Option value="Qun 1">Qun 1</Option>
                                  <Option value="Qun 2">Qun 2</Option>
                                  <Option value="Qun 3">Qun 3</Option>
                                  <Option value="Qun 4">Qun 4</Option>
                                  <Option value="Qun 5">Qun 5</Option>
                                  <Option value="Qun 6">Qun 6</Option>
                                  <Option value="Qun 7">Qun 7</Option>
                                  <Option value="Qun 8">Qun 8</Option>
                                  <Option value="Qun 9">Qun 9</Option>
                                  <Option value="Qun 10">Qun 10</Option>
                                  <Option value="Qun 11">Qun 11</Option>
                                  <Option value="Qun 12">Qun 12</Option>
                                  <Option value="Qun Th c">Qun Th c</Option>
                                  <Option value="Qun Bnh Thnh">Qun Bnh Thnh</Option>
                                  <Option value="Qun Tn Bnh">Qun Tn Bnh</Option>
                                  <Option value="Qun G Vp">Qun G Vp</Option>
                              </Select>
                              )}
                          </Form.Item>
                           <Form.Item label="Lp" hasFeedback>
                              {getFieldDecorator("classTeach", {
                              rules: [
                                  {
                                  required: true,
                                  message: "Vui lng chn lp!"
                                  }
                              ]
                              })(
                              <Select placeholder="Chn lp...">
                                  <Option value="Lp 1">Lp 1</Option>
                                  <Option value="Lp 2">Lp 2</Option>
                                  <Option value="Lp 3">Lp 3</Option>
                                  <Option value="Lp 4">Lp 4</Option>
                                  <Option value="Lp 5">Lp 5</Option>
                                  <Option value="Lp 6">Lp 6</Option>
                                  <Option value="Lp 7">Lp 7</Option>
                                  <Option value="Lp 8">Lp 8</Option>
                                  <Option value="Lp 9">Lp 9</Option>
                                  <Option value="Lp 10">Lp 10</Option>
                                  <Option value="Lp 11">Lp 11</Option>
                                  <Option value="Lp 12">Lp 12</Option>
                              </Select>
                              )}
                          </Form.Item>
                          <Form.Item label="Mn hc: ">
                              {getFieldDecorator("subjects", {
                              rules: [
                                  {
                                  required: true,
                                  message: "Vui lng nhp mn hc!"
                                  }
                              ]
                              })(<Input placeholder="Nhp mn hc cn ng k, VD: ton, l,..." />)}
                          </Form.Item>
                          <Form.Item label="Thi gian hc: ">
                              {getFieldDecorator("time", {
                              rules: [
                                  {
                                  required: true,
                                  message: "Please input your time"
                                  }
                              ]
                              })(<Input placeholder="VD: T2-T4-T6, 17h-19h" />)}
                          </Form.Item>
                          <Form.Item label="Yu cu cp bc:" hasFeedback>
                              {getFieldDecorator("requireLevel", {
                              rules: [
                                  {
                                  required: true,
                                  message: "Please select your require!"
                                  }
                              ]
                              })(
                              <Select placeholder="Hin l...">
                                  <Option value="Sinh vin">Sinh vin</Option>
                                  <Option value="Gio vin">Gio vin</Option>
                                  <Option value="C nhn">C nhn</Option>
                                  <Option value="Thc s">Thc s</Option>
                                  <Option value="Tin s">Tin s</Option>
                              </Select>
                              )}
                          </Form.Item>
                          <Form.Item label="Yu cu gii tnh:" hasFeedback>
                              {getFieldDecorator("requireGender", {
                              rules: [
                                  {
                                  required: true,
                                  message: "Please select your require!"
                                  }
                              ]
                              })(
                              <Select placeholder="Gii tnh l...">
                                  <Option value="Nam">Nam</Option>
                                  <Option value="N">N</Option>
                                  <Option value="Khng yu cu">Khng yu cu</Option>
                              </Select>
                              )}
                          </Form.Item>
                          <Form.Item label="Lng (ng/thng): ">
                              {getFieldDecorator("salary", {
                              rules: [
                                  {
                                  required: true,
                                  message: "Please input your salary"
                                  }
                              ]
                              })(<Input placeholder="VD: 1500000" />)}
                          </Form.Item>
                          <Form.Item>
                              <Button type="primary" htmlType="submit">
                              ng k
                              </Button>
                          </Form.Item>
                      </Form>
                  </div>
              </div>
          </div>
      );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onAddClass: (classInfo, history) =>{
      dispatch(actAddClassRequest(classInfo, history));
    }
  }
}
const RegisterToFindTutorForm = Form.create({name: "findtutor-form"})(RegisterToFindTutorPage);
export default connect(null, mapDispatchToProps)(RegisterToFindTutorForm);
