import React, { Component } from 'react';
import { connect } from "react-redux";
import {actSearchInputRequest} from './../actions/index';

class FormSearch extends Component {
    constructor(props){
        super(props);
        this.state = {
            selectClass: '',
            selectSubject: '',
            selectDistrict: '',
        }
    }
    onChange = (e) => {
        var target = e.target;
        var name = target.name;
        var value = target.value;
        this.setState({
            [name] : value
        })
    }
    onSave = (e) => {
        e.preventDefault();
        var {selectClass, selectSubject, selectDistrict} = this.state;
        let search = {
            classTeach: selectClass,
            subject: selectSubject,
            district: selectDistrict,
            isSearch: true
        }
        this.props.onSearchClasses(search);
        this.props.onSearchInput(search);
    }
    render() {
        return (
            <div className="row">
                <form className="form-search" onSubmit = {this.onSave}>
                    <div className="form-row">
                        <div className="form-group col-md-3">
                            <select id="lop" name = 'selectClass' onChange = {this.onChange} className="form-control">
                                <option value="">...Chn lp...</option>
                                <option value="Lp 1">Lp 1</option>
                                <option value="Lp 2">Lp 2</option>
                                <option value="Lp 3">Lp 3</option>
                                <option value="Lp 4">Lp 4</option>
                                <option value="Lp 5">Lp 5</option>
                                <option value="Lp 6">Lp 6</option>
                                <option value="Lp 7">Lp 7</option>
                                <option value="Lp 8">Lp 8</option>
                                <option value="Lp 9">Lp 9</option>
                                <option value="Lp 10">Lp 10</option>
                                <option value="Lp 11">Lp 11</option>
                                <option value="Lp 12">Lp 12</option>
                            </select>
                        </div>
                        <div className="form-group col-md-3">
                            <select id="mon" className="form-control" name = 'selectSubject' onChange = {this.onChange}>
                                <option value="">...Chn mn hc...</option>
                                <option value="Ton">Ton</option>
                                <option value="L">L</option>
                                <option value="Ha">Ha</option>
                                <option value="Vn">Vn</option>
                                <option value="Ting Anh">Ting Anh</option>
                                <option value="Sinh">Sinh</option>
                                <option value="S">S</option>
                                <option value="a">a</option>
                                <option value="Bo bi">Bo bi</option>
                                <option value="Ting Vit">Ting Vit</option>
                                <option value="Tin hc">Tin hc</option>
                                <option value="V">V</option>
                            </select>
                        </div>
                        <div className="form-group col-md-3">
                            <select id="mon" className="form-control" name = 'selectDistrict' onChange = {this.onChange}>
                                <option value="">...Chn qun...</option>
                                <option value="Qun 1">Qun 1</option>
                                <option value="Qun 2">Qun 2</option>
                                <option value="Qun 3">Qun 3</option>
                                <option value="Qun 4">Qun 4</option>
                                <option value="Qun 5">Qun 5</option>
                                <option value="Qun 6">Qun 6</option>
                                <option value="Qun 7">Qun 7</option>
                                <option value="Qun 8">Qun 8</option>
                                <option value="Qun 9">Qun 9</option>
                                <option value="Qun 10">Qun 10</option>
                                <option value="Qun 11">Qun 11</option>
                                <option value="Qun 12">Qun 12</option>
                                <option value="Qun Th c">Qun Th c</option>
                                <option value="Qun Bnh Thnh">Qun Bnh Thnh</option>
                                <option value="Qun Tn Bnh">Qun Tn Bnh</option>
                                <option value="Qun G Vp">Qun G Vp</option>
                            </select>
                        </div>
                        <div className="form-group col-md-3">
                            <button 
                                type="submit" 
                                name="submit" 
                                className="btn btn-primary" 
                                style={{ width: '100%' }}>
                                    Tm kim
                            </button>
                        </div>
                    </div>
                </form>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {
        search: state.search
    }
}
const mapDispatchToProps = dispatch => {
    return {
      onSearchInput: (search) => {
        dispatch(actSearchInputRequest(search));
      }
    };
  };
export default connect(mapStateToProps, mapDispatchToProps)(FormSearch);