import React, { Component } from "react";
import "./../style/signup.css";
import "antd/dist/antd.css";
import {
  Form,
  Input,
  Radio,
  Button,
  Select,
  Upload,
  Icon,
  Checkbox,
  Row,
  Col,
  notification
} from "antd";
import callApi from "./../utils/apiCaller";
import { Link } from "react-router-dom";
import { actUploadImageRequest, actAddTutorRequest } from "./../actions/index";

const { Option } = Select;
const { TextArea } = Input;

class RegisterToMakeTutorPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      confirmDirty: false
    };
  }
  handleSubmit = e => {
    e.preventDefault();
    var { history } = this.props;
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log("Received values of form: ", values);

        let formData = new FormData();
        let imagedata = values.file[0];
        formData.append("file", imagedata.originFileObj);
        let tutorInfo = {
          gender: values.gender,
          yearOfBirth: values.yearOfBirth,
          image: imagedata.originFileObj.name,
          major: values.major,
          college: values.college,
          graduationYear: values.graduationYear,
          level: values.level,
          subjects: values.subjects.join(","),
          classes: values.classes.join(","),
          districtCanTeach: values.districts.join(","),
          moreInfo: values.moreInfo
        };

        //create tutor
        callApi("api/users/signUp?type=GIASU", "POST", {
          name: values.name,
          phone: values.phone,
          address: values.address,
          email: values.email,
          password: values.password
        })
          .then(res => {
            console.log(res);
            if (res.data.status === 200) {
              let idUser = res.data.result.idUser;
              //upload image
              actUploadImageRequest(formData)
                .then(res => {
                  if (res.status === 200) {
                    //create tutor
                    actAddTutorRequest(tutorInfo, idUser, history);
                    
                  }
                })
                .catch(error => {
                  notification.error({
                    message: "Error Upload",
                    description: error.message
                  });
                });
            }
          })
          .catch(error => {
            notification.error({
              message: "Error create account",
              description: error.message
            });
          });
      } else {
        notification.error({
          message: "Error",
          description: "Error signup"
        });
      }
    });
  };

  normFile = e => {
    console.log("Upload event:", e);
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };

  handleConfirmBlur = e => {
    const { value } = e.target;
    this.setState({ confirmDirty: this.state.confirmDirty || !!value });
  };

  compareToFirstPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && value !== form.getFieldValue("password")) {
      callback("Hai mt khu khng khp!");
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(["confirm"], { force: true });
    }
    callback();
  };

  render() {
    const { getFieldDecorator } = this.props.form;

    return (
      <div className="col-lg-9 col-md-9 col-sm-9">
        <div className="row">
          <div className="panel-heading">
            <i className="fa fa-address-book" style={{ marginLeft: "5px" }} />
            ng k lm gia s
          </div>
        </div>
        <div className="row" id="row-form">
          <div className="panel-body">
            <Form
              layout="vertical"
              className="formal-form"
              onSubmit={this.handleSubmit}
            >
              <p>
                Nu bn  c ti khon, vui lng ng nhp
                <Link to="/login"> Ti y</Link>
              </p>
              <Form.Item label="H tn: ">
                {getFieldDecorator("name", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp h tn!"
                    }
                  ]
                })(<Input placeholder="Nhp h tn..." />)}
              </Form.Item>
              <Form.Item label="S in thoi:">
                {getFieldDecorator("phone", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp s in thoi!"
                    },
                    {
                      pattern: /^\d{10,11}$/,
                      message:
                        "S in thoi ch cho php kiu s. Yu cu 10 s hoc 11 s!"
                    }
                  ]
                })(<Input placeholder="Nhp s in thoi..." />)}
              </Form.Item>
              <Form.Item label="Mt khu: " hasFeedback>
                {getFieldDecorator("password", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp mt khu!"
                    },
                    {
                      validator: this.validateToNextPassword
                    },
                    {
                      pattern:
                        "^(?=.*\\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z!@#$%^&*()]{6,}$",
                      message:
                        "Mt khu t nht phi c 6 k t, bao gm: ch hoa, ch thng v s!"
                    }
                  ]
                })(
                  <Input.Password
                    placeholder="Nhp mt khu..."
                    autoComplete="off"
                  />
                )}
              </Form.Item>
              <Form.Item label="Nhp li mt khu: " hasFeedback>
                {getFieldDecorator("confirm", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp li mt khu!"
                    },
                    {
                      validator: this.compareToFirstPassword
                    }
                  ]
                })(
                  <Input.Password
                    placeholder="Nhp li mt khu..."
                    onBlur={this.handleConfirmBlur}
                    autoComplete="off"
                  />
                )}
              </Form.Item>
              <Form.Item label="E-mail: ">
                {getFieldDecorator("email", {
                  rules: [
                    {
                      type: "email",
                      message: "Khng ng nh dng email!"
                    }
                  ]
                })(<Input placeholder="Vd: abc@gmail.com" />)}
              </Form.Item>
              <Form.Item label="a ch: ">
                {getFieldDecorator("address", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp a ch"
                    }
                  ]
                })(<Input placeholder="Nhp a ch..." />)}
              </Form.Item>
              <Form.Item label="Gii tnh: ">
                {getFieldDecorator("gender", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp gii tnh"
                    }
                  ]
                })(
                  <Radio.Group>
                    <Radio value="Nam">Nam</Radio>
                    <Radio value="N">N</Radio>
                  </Radio.Group>
                )}
              </Form.Item>
              <Form.Item label="Nm sinh" hasFeedback>
                {getFieldDecorator("yearOfBirth", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng chn nm sinh!"
                    }
                  ]
                })(
                  <Select placeholder="Chn nm sinh...">
                    <Option value="2005">2005</Option>
                    <Option value="2004">2004</Option>
                    <Option value="2003">2003</Option>
                    <Option value="2002">2002</Option>
                    <Option value="2001">2001</Option>
                    <Option value="2000">2000</Option>
                    <Option value="1999">1999</Option>
                    <Option value="1998">1998</Option>
                    <Option value="1997">1997</Option>
                    <Option value="1996">1996</Option>
                    <Option value="1995">1995</Option>
                    <Option value="1994">1994</Option>
                    <Option value="1993">1993</Option>
                    <Option value="1992">1992</Option>
                    <Option value="1991">1991</Option>
                    <Option value="1990">1990</Option>
                    <Option value="1989">1989</Option>
                    <Option value="1988">1988</Option>
                    <Option value="1987">1987</Option>
                    <Option value="1986">1986</Option>
                    <Option value="1985">1985</Option>
                    <Option value="1984">1984</Option>
                    <Option value="1983">1983</Option>
                  </Select>
                )}
              </Form.Item>
              <Form.Item label="nh th">
                {getFieldDecorator("file", {
                  valuePropName: "fileList",
                  getValueFromEvent: this.normFile,
                  rules: [
                    {
                      required: true,
                      message: "Vui lng chn nh th!"
                    }
                  ]
                })(
                  <Upload name="file" listType="picture">
                    <Button>
                      <Icon type="upload" /> Click to upload
                    </Button>
                  </Upload>
                )}
              </Form.Item>
              <Form.Item label="Trng o to: ">
                {getFieldDecorator("college", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp trng o to!"
                    }
                  ]
                })(<Input placeholder="Nhp tn trng..." />)}
              </Form.Item>
              <Form.Item label="Ngnh hc ">
                {getFieldDecorator("major", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp ngnh hc!"
                    }
                  ]
                })(<Input placeholder="Nhp ngnh hc..." />)}
              </Form.Item>
              <Form.Item label="Nm tt nghip ">
                {getFieldDecorator("graduationYear", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp nm tt nghip!"
                    }
                  ]
                })(<Input placeholder="Nhp nm tt nghip..." />)}
              </Form.Item>
              <Form.Item label="Hin l" hasFeedback>
                {getFieldDecorator("level", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp cp bc!"
                    }
                  ]
                })(
                  <Select placeholder="Hin l...">
                    <Option value="Sinh vin">Sinh vin</Option>
                    <Option value="Gio vin">Gio vin</Option>
                    <Option value="C nhn">C nhn</Option>
                    <Option value="Thc s">Thc s</Option>
                    <Option value="Tin s">Tin s</Option>
                  </Select>
                )}
              </Form.Item>
              <Form.Item label="Mn dy">
                {getFieldDecorator("subjects", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng chn mn dy!"
                    }
                  ]
                })(
                  <Checkbox.Group style={{ width: "100%" }}>
                    <Row>
                      <Col span={8}>
                        <Checkbox value="Ton">Ton</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="L">L</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Ho">Ho</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Vn">Vn</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Ting Anh">Ting Anh</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Sinh">Sinh</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="S">S</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="a">a</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Bo bi">Bo bi</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Ting Vit">Ting Vit</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Tin hc">Tin hc</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="V">V</Checkbox>
                      </Col>
                    </Row>
                  </Checkbox.Group>
                )}
              </Form.Item>
              <Form.Item label="Lp dy">
                {getFieldDecorator("classes", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng chn lp dy!"
                    }
                  ]
                })(
                  <Checkbox.Group style={{ width: "100%" }}>
                    <Row>
                      <Col span={8}>
                        <Checkbox value="Lp 1">Lp 1</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 2">Lp 2</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 3">Lp 3</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 4">Lp 4</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 5">Lp 5</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 6">Lp 6</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 7">Lp 7</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 8">Lp 8</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 9">Lp 9</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 10">Lp 10</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 11">Lp 11</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Lp 12">Lp 12</Checkbox>
                      </Col>
                    </Row>
                  </Checkbox.Group>
                )}
              </Form.Item>
              <Form.Item label="Khu vc dy">
                {getFieldDecorator("districts", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng chn khu vc dy!"
                    }
                  ]
                })(
                  <Checkbox.Group style={{ width: "100%" }}>
                    <Row>
                      <Col span={8}>
                        <Checkbox value="Qun 1">Qun 1</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 2">Qun 2</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 3">Qun 3</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 4">Qun 4</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 5">Qun 5</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 6">Qun 6</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 7">Qun 7</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 8">Qun 8</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 9">Qun 9</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 10">Qun 10</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 11">Qun 11</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun 12">Qun 12</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun Th c">Qun Th c</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun Bnh Thnh">
                          Qun Bnh Thnh
                        </Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun Tn Bnh">Qun Tn Bnh</Checkbox>
                      </Col>
                      <Col span={8}>
                        <Checkbox value="Qun G Vp">Qun G Vp</Checkbox>
                      </Col>
                    </Row>
                  </Checkbox.Group>
                )}
              </Form.Item>
              <Form.Item label="Thng tin thm">
                {
                  (getFieldDecorator("moreInfo"),
                  (<TextArea autoSize={{ minRows: 3, maxRows: 5 }} />))
                }
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  ng k
                </Button>
              </Form.Item>
            </Form>
          </div>
        </div>
      </div>
    );
  }
}

const RegisterToMakeTutorForm = Form.create({ name: "maketutor-form" })(
  RegisterToMakeTutorPage
);
export default RegisterToMakeTutorForm;
