import React, { Component } from "react";
import "antd/dist/antd.css";
import {Form, Input, Button, notification} from "antd";
import "./../style/signup.css";
import callApi from './../utils/apiCaller';

class SigupPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
        confirmDirty: false
    };
  
  }
    handleSubmit = e => {
        e.preventDefault();
        var {history} = this.props;
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log("Received values of form: ", values);
            }
            callApi(`api/users/signUp?type=PHUHUYNH`, 'POST', {
                name: values.name,
                phone: values.phone,
                address: values.address,
                email: values.email,
                password: values.password
            }).then(res => {
                console.log(res);
              if (res.data.status === 200){
                history.push('/login');
                notification.success({
                  message: 'Success',
                  description: 'Signup successfully!'
                })
              }
            }).catch(error => {
              notification.error({
                  message: 'Error signup',
                  description: error.message
              });   
          });
        });
    };

    handleConfirmBlur = e => {
      const { value } = e.target;
      this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    };
  
    compareToFirstPassword = (rule, value, callback) => {
      const { form } = this.props;
      if (value && value !== form.getFieldValue("password")) {
          callback("Hai mt khu khng khp!");
      } else {
          callback();
      }
    };
  
    validateToNextPassword = (rule, value, callback) => {
      const { form } = this.props;
      if (value && this.state.confirmDirty) {
          form.validateFields(["confirm"], { force: true });
      }
      callback();
    };  
    

  render() {
    const { getFieldDecorator } = this.props.form;

    return (
      <div className="col-lg-9 col-md-9 col-sm-9">
        <div className="row">
          <div className="panel-heading">
            <i className="fa fa-user-plus" style={{ marginLeft: "5px" }} />
            ng k ti khon ph huynh
          </div>
        </div>
        <div className="row">
          <div className="panel-body">
            <Form
              layout="vertical"
              className="formal-form"
              onSubmit={this.handleSubmit}
            >
              <Form.Item label="H tn: ">
                {getFieldDecorator("name", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp h tn!"
                    }
                  ]
                })(<Input placeholder="Nhp h tn..." />)}
              </Form.Item>
              <Form.Item label="S in thoi:">
                {getFieldDecorator("phone", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp s in thoi!"
                    },
                    {
                      pattern: /^\d{10,11}$/,
                      message: "S in thoi ch cho php kiu s. Yu cu 10 s hoc 11 s!"
                    }
                    
                  ]
                })(<Input  placeholder="Nhp s in thoi..." />)}
              </Form.Item>
              <Form.Item label="Mt khu: " hasFeedback>
                {getFieldDecorator("password", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp mt khu!"
                    },
                    {
                      validator: this.validateToNextPassword,
                    },
                    {
                      pattern: "^(?=.*\\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z!@#$%^&*()]{6,}$",
                      message: "Mt khu t nht phi c 6 k t, bao gm: ch hoa, ch thng v s!"
                    },
                  ]
                })(<Input.Password placeholder="Nhp mt khu..." autoComplete="off"/>)}
              </Form.Item>
              <Form.Item label="Nhp li mt khu: " hasFeedback>
                {getFieldDecorator("confirm", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp li mt khu!"
                    },
                    {
                      validator: this.compareToFirstPassword
                    }
                  ]
                })(
                  <Input.Password
                    placeholder="Nhp li mt khu..."
                    onBlur={this.handleConfirmBlur}
                    autoComplete="off"
                  />
                )}
              </Form.Item>
              <Form.Item label="E-mail: ">
                {getFieldDecorator("email", {
                  rules: [
                    {
                      type: "email",
                      message: "Khng ng nh dng email!"
                    }
                  ]
                })(<Input  placeholder="Vd: abc@gmail.com" />)}
              </Form.Item>
              <Form.Item label="a ch: ">
                {getFieldDecorator("address", {
                  rules: [
                    {
                      required: true,
                      message: "Vui lng nhp a ch"
                    }
                  ]
                })(<Input placeholder="Nhp a ch..." />)}
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  ng k
                </Button>
              </Form.Item>
            </Form>
          </div>
        </div>
      </div>
    );
  }
}

const SigupForm = Form.create({ name: "sigup-form" })(SigupPage);

export default SigupForm;
