import React, { Component } from "react";
import ClassDetail from "./../components/ClassDetail";
import {
  actGetClassRequest,
  actTutorRegisterClassRequest,
  actFetchTutorRegisterClassRequest
} from "./../actions/index";
import { connect } from "react-redux";
import { DatePicker } from "antd";
import { Form, Input, Button, Select } from "antd";
import ClassRegisterList from "./../components/ClassRegisterList";
import ClassRegisterItem from "./../components/ClassRegisterItem";
const { Option } = Select;
const { TextArea } = Input;

class RegisterClassPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      content: [],
    };
  }
  componentDidMount() {
    var { match } = this.props;
    if (match) {
      var id = match.params.id;
      this.props.onRegisterClass(id);
      this.props.onFetchTutorRegisterClass(id);
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const fieldsValue = {
        ...values,
        dateReceive: values["dateReceive"].format("YYYY-MM-DD")
      };
      let idClass = this.props.classRegister.idClass;
      this.props.onTutorRegisterClass(fieldsValue, idClass);
    });
  };

  render() {
    let { classRegister } = this.props;
    let  {tutorRegisterClass}  = this.props;
    const { getFieldDecorator } = this.props.form;
    let {idUser} = this.state;
    return (
      <div className="col-lg-9 col-md-9 col-sm-9">
        <div className="row">
          <div className="panel-heading">
            <i className="fa fa-fw fa-user" style={{ marginLeft: "5px" }} />
            Lp ang cn gia s
          </div>
        </div>
        <div className="row">
          <ClassDetail classRegister={classRegister}></ClassDetail>
          <div className="col-sm-4 k-nhan-lop">
            <h4 className="nhanlop">NG K NHN LP</h4>
            <Form onSubmit={this.handleSubmit}>
              <Form.Item>
                {getFieldDecorator("dateReceive", {
                  rules: [
                    {
                      required: true,
                      message: "Nhp ngy nhn lp"
                    }
                  ]
                })(<DatePicker placeholder="Ngy nhn lp..." />)}
              </Form.Item>
              <Form.Item hasFeedback>
                {getFieldDecorator("payments", {
                  rules: [
                    {
                      required: true,
                      message: "Nhp hnh thc thanh ton!"
                    }
                  ]
                })(
                  <Select placeholder="Hnh thc thanh ton...">
                    <Option value="Chuyn khon">Chuyn khon</Option>
                    <Option value="n trung tm">n trung tm</Option>
                  </Select>
                )}
              </Form.Item>
              <Form.Item>
                {
                  (getFieldDecorator("moreRequire"),
                  (
                    <TextArea
                      placeholder="Yu cu thm nu c"
                      autoSize={{ minRows: 3, maxRows: 3 }}
                    />
                  ))
                }
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  ng k
                </Button>
              </Form.Item>
            </Form>
          </div>
        </div>
        {tutorRegisterClass.length <= 0 ? (
          <div className="row luu-y">
            {" "}
            <strong>Hin ti cha c gia s no ng k nhn lp ny</strong>
          </div>
        ) : (
          <ClassRegisterList>
            {this.showClassRegisterList(tutorRegisterClass, idUser)}
          </ClassRegisterList>
        )}
        <div className="row luu-y">
          <p style={{ fontStyle: "italic" }}>
            <strong style={{ color: "red" }}>(*)</strong>Cho php ti a 5 ngi
            ng k. Ch giao lp cho 1 ngi  iu kin ng l ph trc.
          </p>
          <br />
          <br />
          <p>
            <strong style={{ color: "red" }}>Lu </strong>: Trung tm{" "}
            <span style={{ fontWeight: "bold" }}>Gia s nh Dng</span> u tin
            ngi  iu kin, ng l ph sm. Trc khi chuyn khon hoc ti
            trung tm theo gi bn  hn hy gi s{" "}
            <span style={{ fontWeight: "bold" }}>
              0902684422 hoc 0902504900
            </span>{" "}
             kim tra trng thi lp chnh xc.
          </p>
        </div>

      </div>
    );
  }
 
  showClassRegisterList(tutorRegisterClass, idUser) {
    var result = null;
    if (tutorRegisterClass === undefined) {
      return;
    }
    if (tutorRegisterClass.length > 0) {
      result = tutorRegisterClass.map((tutorRegisterClassItem, index) => {
        return (
          <ClassRegisterItem
            key={index}
            tutorRegisterClassItem={tutorRegisterClassItem}
          />
        );
      });
    }
    return result;
  }
}

const mapStateToProps = state => {
  return {
    classRegister: state.classRegister,
    tutorRegisterClass: state.tutorRegisterClass,
    auth: state.auth
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onFetchTutorRegisterClass: idClass => {
      dispatch(actFetchTutorRegisterClassRequest(idClass));
    },
    onRegisterClass: id => {
      dispatch(actGetClassRequest(id));
    },
    onTutorRegisterClass: (classRegisterInfo, idClass) => {
      dispatch(actTutorRegisterClassRequest(classRegisterInfo, idClass));
    }
  };
};

const RegisterForm = Form.create({ name: "register-form" })(RegisterClassPage);
export default connect(mapStateToProps, mapDispatchToProps)(RegisterForm);
