import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import {actSearchInputRequest} from './../actions/index';
import { connect } from "react-redux";

class QuickMenu extends Component {
    handleOnClick = () => {
        let searchInfo = {
          classTeach: "",
          subject: "",
          district: "",
          isSearch: false
        };
        this.props.onSearchInput(searchInfo);
    
    }
    render() {
        return (
            <div className="row menu-nhanh">
                <div className="col-sm-6" style={{ borderRight: 'solid 2px#fff470' }}>
                    <h3 className="tieude">Dnh cho ph huynh</h3>
                    <ul className="menu-phuhuynh">
                        <li>
                            <Link 
                                to = '/tutor-fee'>
                                Hc ph gia s
                            </Link>
                        </li>
                        <li>
                            <Link 
                                to='/signup'>
                                ng k tm gia s
                            </Link>
                        </li>
                    </ul>
                    <div className="bottom-lienhe">
                        <p className="lienhe">Ph huynh lin h</p>
                        <p className="dtlienhe">0933160610</p>
                    </div>
                </div>
                <div className="col-sm-6">
                    <h3 className="tieude">Dnh cho gia s</h3>
                    <ul className="menu-phuhuynh">
                        <li>
                            <Link 
                                to='/class-list' onClick={this.handleOnClick}>Lp dy mi
                            </Link>
                        </li>
                        <li>
                            <Link to='/make-tutor'>
                                ng k lm gia s
                            </Link>
                        </li>
                    </ul>
                    <div className="bottom-lienhe">
                        <p className="lienhe">Gia s lin h</p>
                        <p className="dtlienhe">0974315184</p>
                    </div>
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
      onSearchInput: (search) => {
        dispatch(actSearchInputRequest(search));
      },
    };
  };

export default connect(null, mapDispatchToProps)(QuickMenu);